.toggle-btn {
  background-color: transparent;
  border: none;
  cursor: pointer;

  &:active {
    transform: scale(0.8);
  }

  &:focus {
    outline: none;
  }

  &__icon {
    @include sprite($toggle);
    display: inline-block;
    content:'';
    margin-left: 3px;
    margin-top: 2px;
  }
}