.slick-slide {
  &:focus {
    outline: none;
  }
}

.slick-trace .slick-prev,
.slick-trace .slick-next {
    height: 41px;
    width: 23px;
    //убираем, чтоб при нажимании на кнопку, она не опускалась вниз
    transform: none;

    &:before {
    display: inline-block;
    content: "";
  }

   &:active {
    transform: scale(0.8);
  }
}

.slick-trace .slick-prev {
  left: -32px;
  top: 135px;

  @media (min-width: $screen-sm) {top: 275px;}

  &:before {
    @include sprite($arrow-prev);
  }
}

.slick-trace .slick-next {
  right: -32px;
  top: 135px;

  @media (min-width: $screen-sm) {top: 275px;}

  &:before {
    @include sprite($arrow-next);
  }
}


